.bullhorn {
	h1, h2, h3, h4, h5, h6 {
		color: colour("dark-royal");
	}
	font-family: "Manrope", Sans-serif;
	* {
	}
}
.bullhorn-search-form {
	padding: 45px 0;
	@media (min-width: 768px) {
		padding: 60px 0;
	}
	&__field {
		position: relative;
		border-radius: 5px;
		margin: 15px 0;
		input,
		select {
			appearance: none;
			color: colour("text");
			font-size: 16px;
			line-height: 1.4;
			width: 100%;
			height: 100%;
			margin: 0;
			padding: 15px 30px;
			background-color: transparent;
			border: none;
			outline: none;
			&::placeholder {
				color: colour("dark-grey");
			}
		}
		&--input {
			background: colour("white");
			border: 2px solid colour("white");
			&:hover,
			&:focus-within {
				border: 2px solid colour("grey");
			}
			i {
				color: colour("red-orange");
				margin: 0 30px;
			}
		}
		&--select {
			background: colour("white");
			border: 2px solid colour("white");
			position: relative;
			&:hover,
			&:focus-within {
				border: 2px solid colour("grey");
			}
			select {
				position: relative;
				z-index: 1;
			}
			i {
				align-items: center;
				display: flex;
				padding: 0 30px;
				position: absolute;
				z-index: 0;
				top: 0;
				inset-inline-end: 0;
				bottom: 0;

			}

		}
		&--submit {
			input {
				background-color: colour("red-orange");
				color: colour("white");
				font-weight: 600;
			}
		}
	}
}
.bullhorn-search-results {
	background: colour("white");
	padding: 90px 0;
	font-size: 16px;
	@media (max-width: 767px) {
		padding: 45px 0;
	}
	@media (max-width: 1366px) {
		padding: 60px 0;
	}
	&__item {
		background: colour("white");
		border-top: 1px solid colour("grey");
		border-left: 1px solid colour("white");
		border-right: 1px solid colour("white");
		color: colour("text");
		display: block;
		padding: 30px;
		transition: all 0.3s ease-in-out;
		@media (max-width: 767px) {
			padding: 15px 30px;
		}
		&:hover,
		&:focus {
			background: colour("light-grey");
			border-left: 1px solid colour("grey");
			border-right: 1px solid colour("grey");
		}
		&:last-child {
			border-bottom: 1px solid colour("grey");
		}
		&--job-title {
		}
		&--job-location {
			color: colour("dark-grey");
		}
		&--job-date {
			color: colour("dark-grey");
		}
	}
}
.bullhorn-search-pagination {
	padding: 60px 0;
	font-size: 16px;
	@media (max-width: 767px) {
		padding: 30px 0;
	}
	@media (max-width: 1366px) {
		padding: 45px 0;
	}

	a {
		display: block;
		padding: 15px 25px;
		@media (max-width: 767px) {
			padding: 15px 15px;
		}
		@media (max-width: 1024px) {
			padding: 15px 20px;
		}
	}
	&__button {
		border-radius: 5px;
		color: colour("dark-grey");
		&--back,
		&--next {
			background: colour("white");
			color: colour("text");
			width: 200px;
			text-align: center;
			@media (max-width: 767px) {
				width: 100%;
				span {
					display: none;
				}
			}

		}
		&--back {
			i {
				margin-inline-end: 10px;
				@media (max-width: 767px) {
					// margin-inline-end: 0;
				}
			}
		}
		&--next {
			i {
				margin-inline-start: 10px;
				@media (max-width: 767px) {
					// margin-inline-start: 0;
				}
			}
		}
		&--number {
			color: colour("dark-grey");
			&:hover{
				background: colour("white");
				color: colour("text");
			}
		}
		&--disabled {
			opacity: 0.6;
			pointer-events: none;
		}
		&--active {
			color: colour("text");
			font-weight: 700;
		}
	}
}
